import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Paper,
  Typography,
  TextField,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from '@mui/material';

import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import Sidebar from '../components/Sidebar';

const Settings = () => {

  const [monhocs, setMonHocs] = useState([]);
  const [openModal, setOpenModal] = useState(false);
  const [newMonHoc, setNewMonHoc] = useState({
    MaMH: '',
    TenMH: '',
    SoTinChi: '',
    HeSoCC: '',
    HeSoGK: '',
    HeSoCK: '',
  });

 

  useEffect(() => {
    const fetchMonHoc = async () => {
      try {
        const response = await fetch('https://be1-fizs.onrender.com/monhoc');
        const data = await response.json();
        setMonHocs(data);
      } catch (error) {
        console.error('Li khi ti d liu lp:', error);
      }
    };
    fetchMonHoc();
  }, []);

  const handleDeleteMonHoc = async (MaMH) => {
    const isConfirmed = window.confirm('Bn c chc chn mun xa mn hc ny khng?');
    
    if (!isConfirmed) {
      return;
    }
  
    try {
      const response = await fetch(`https://be1-fizs.onrender.com/monhoc/${MaMH}`, {
        method: 'DELETE',
      });
  
      if (!response.ok) {
        console.error(`Error deleting monhoc: ${response.statusText}`);
        throw new Error('Li khi xa mn hc');
      }
  
      // Ch cp nht UI sau khi xa thnh cng
      setMonHocs((prevMonHocs) => prevMonHocs.filter((monhoc) => monhoc.MaMH !== MaMH));
    } catch (error) {
      console.error('Li xa mn hc:', error);
    }
  };
  
  const handleOpenModal = () => {
    setOpenModal(true);
  };

  const handleCloseModal = () => {
    setOpenModal(false);
  };

  const handleChangeNewMonHoc = (field) => (event) => {
    setNewMonHoc({
      ...newMonHoc,
      [field]: event.target.value,
    });
  };

  const handleAddMonHoc = async () => {
    try {
      // Gi request  thm mn hc mi vo database
      const response = await fetch('https://be1-fizs.onrender.com/monhoc', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(newMonHoc),
      });
  
      if (!response.ok) {
        throw new Error('Li khi thm mn hc');
      }
      alert("Thm mn hc thnh cng!")
      // Sau khi thm thnh cng, ti li ton b danh sch mn hc
      const fetchMonHoc = async () => {
        try {
          const response = await fetch('https://be1-fizs.onrender.com/monhoc');
          const data = await response.json();
          setMonHocs(data);
        } catch (error) {
          console.error('Li khi ti d liu mn hc:', error);
        }
      };
      fetchMonHoc();
  
      // ng modal v reset form
      handleCloseModal();
      setNewMonHoc({
        MaMH: '',
        TenMH: '',
        SoTinChi: '',
        HeSoCC: '',
        HeSoGK: '',
        HeSoCK: '',
      });
    } catch (error) {
      console.error('Li khi thm mn hc:', error);
    }
  };
  

  return (
    <Box sx={{ display: 'flex' }}>
      <Sidebar />
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          ml: '240px',
          bgcolor: '#f5f5f5',
          minHeight: '100vh',
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="h4" sx={{ mb: 4 }}>
            Ci t h thng
          </Typography>

          {/* Qun l mn hc section */}
          <Paper sx={{ p: 3 }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
              <Typography variant="h6">Qun l mn hc</Typography>
              <Button
                variant="contained"
                color="primary"
                startIcon={<AddIcon />}
                onClick={handleOpenModal}
              >
                Thm mn hc
              </Button>
            </Box>

            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>M mn hc</TableCell>
                    <TableCell>Tn mn hc</TableCell>
                    <TableCell align="center">S tn ch</TableCell>
                    <TableCell align="center">H s Chuyn Cn</TableCell>
                    <TableCell align="center">H s Gia k</TableCell>
                    <TableCell align="center">H s Cui k</TableCell>
                    <TableCell align="center">Thao tc</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {monhocs.map((monhoc) => (
                    <TableRow key={monhoc.MaMH}>
                      <TableCell>{monhoc.MaMH}</TableCell>
                      <TableCell>{monhoc.TenMH}</TableCell>
                      <TableCell align="center">{monhoc.SoTinChi}</TableCell>
                      <TableCell align="center">{monhoc.HeSoCC}</TableCell>
                      <TableCell align="center">{monhoc.HeSoGK}</TableCell>
                      <TableCell align="center">{monhoc.HeSoCK}</TableCell>
                      <TableCell align="center">
                        <IconButton
                          color="error"
                          size="small"
                          onClick={() => handleDeleteMonHoc(monhoc.MaMH)}
                        >
                          <DeleteIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Paper>
        </Container>
      </Box>

      {/* Modal form thm mn hc */}
      <Dialog open={openModal} onClose={handleCloseModal}>
        <DialogTitle>Thm Mn Hc</DialogTitle>
        <DialogContent>
          <TextField
            label="M mn hc"
            value={newMonHoc.MaMH}
            onChange={handleChangeNewMonHoc('MaMH')}
            fullWidth
            sx={{ mb: 2 }}
          />
          <TextField
            label="Tn mn hc"
            value={newMonHoc.TenMH}
            onChange={handleChangeNewMonHoc('TenMH')}
            fullWidth
            sx={{ mb: 2 }}
          />
          <TextField
            label="S tn ch"
            value={newMonHoc.SoTinChi}
            onChange={handleChangeNewMonHoc('SoTinChi')}
            type="number"
            fullWidth
            sx={{ mb: 2 }}
          />
          <TextField
            label="H s Chuyn Cn"
            value={0.1}
            InputProps={{
              readOnly: true,  // Makes the field read-only
            }}
            onChange={handleChangeNewMonHoc('HeSoCC')}
            type="number"
            fullWidth
            sx={{ mb: 2 }}
          />
          <TextField
            label="H s Gia k"
            value={0.3}
            InputProps={{
              readOnly: true,  // Makes the field read-only
            }}
            onChange={handleChangeNewMonHoc('HeSoGK')}
            type="number"
            fullWidth
            sx={{ mb: 2 }}
          />
          <TextField
            label="H s Cui k"
            value={0.6}
            InputProps={{
              readOnly: true,  // Makes the field read-only
            }}
            onChange={handleChangeNewMonHoc('HeSoCK')} type="number" fullWidth />
             </DialogContent> <DialogActions> <Button onClick={handleCloseModal} color="primary"> Hy </Button> <Button onClick={handleAddMonHoc} color="primary"> Thm </Button> </DialogActions> </Dialog> </Box> ); };

export default Settings;